.footer {
    margin-top: 50px;

    &__container {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__top {
        background-color: #282B30;
        padding: 23px 0;

        &__logo__wrapper {
            display: flex;
            align-items: center;

            &__text {
                width: 152px;
                margin-left: 14px;
                font-weight: 700;
                font-size: 15px;
                line-height: 17px;
                color: #FFFFFF;
            }
        }

        &__text {
            width: 307px;
            font-weight: 500;
            font-size: 15px;
            line-height: 18px;
            color: #98A4B7;
        }

        &__wrapper {
            display: flex;
            align-items: center;
            gap: 4px;
        }
    }

    &__bottom {
        background-color: #33383C;

        &__list {
            display: flex;
            justify-content: space-between;
        }

        &__item {
            color: #FFFFFF;
            font-weight: 700;
            font-size: 14px;
            line-height: 17px;

            &__arrow1 {
                color: #FF5469;
                margin-right: 12px;
            }

            &__arrow2 {
                color: #7ED321;
                margin-right: 12px;
            }

            &__arrow3 {
                color: #F8E71C;
                margin-right: 12px;
            }

            &__arrow4 {
                color: #BD10E0;
                margin-right: 12px;
            }

            &__arrow5 {
                color: #FF5469;
                margin-right: 12px;
            }

            &__arrow6 {
                color: #50E3C2;
                margin-right: 12px;
            }

            &__arrow7 {
                color: #F5A623;
                margin-right: 12px;
            }

            &__arrow8 {
                color: #4A90E2;
                margin-right: 12px;
            }

            &__list {
                margin: 12px 0 25px 0;
                margin-left: 22px;
            }

            &__text {

                &__link {
                    text-decoration: none;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 29px;
                    color: #878F99;
                }
            }
        }

        &__wrapper {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 34px;

            &__text {
                font-size: 15px;
                line-height: 18px;
                color: #66717A;

                &__arrow {
                    transform: rotate(90deg);
                    color: #66717A;
                    margin-left: 9px;
                }
            }
        }

        &__end {
            display: flex;
            align-items: center;
            justify-content: space-between;
            border-top: 2px solid #4C565E;
            padding: 27px 0 15px 0;

            &__item {
                &__link {
                    text-decoration: none;
                    font-size: 15px;
                    line-height: 18px;
                    color: #878F99;

                    &:hover {
                        color: #05F3B3;
                    }
                }
            }
        }
    }
}