@font-face {
    font-family: 'Gilroy';
    src: url('./../src/assets/fonts/Gilroy-Regular.woff') format('woff2'),
    url('./../src/assets/fonts/Gilroy-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

