*, ::before, ::after {
    box-sizing: border-box;
}

.container {
    width: 1180px;
    padding-left: 20px;
    padding-right: 20px;
    margin-left: auto;
    margin-right: auto;
}

body {
    margin: 0;
    padding: 0;
    font-size: 16px;
    font-family: 'Gilroy' , "Arial", sans-serif;
}

