.search {
    margin: 16px 0 17px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &__btn {
        border: none;
        outline: none;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        border-radius: 25px;
        padding-right: 30px;
        padding-left: 0;
        background: #19D476;
        color: #FFFFFF;
        cursor: pointer;
        display: flex;
        align-items: center;

        &__circle {
            text-align: center;
            font-size: 28px;
            line-height: 33px;
            border-radius: 50%;
            padding: 6px 13px 5px 15px;
            margin-right: 12px;
            color: #FFFFFF;
            background: #11B463;
        } 
    }
}

.form {
    background: #FFFFFF;
    border: 1px solid rgba(151, 151, 151, 0.496575);
    border-radius: 22.5px;
    width: 872px;
    height: 45px;
    display: flex;
    align-items: center;

    &__select__categoriy {
        background: none;
        outline: none;
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        border: 1px solid rgba(151, 151, 151, 0.496575);
        border-top-left-radius: 22.5px;
        border-bottom-left-radius: 22.5px;
        padding: 13px 18px 14px 30px;
    }

    &__input {
        background: none;
        border: none;
        outline: none;
        padding: 14px 174px 13px 25px;
        border-left: 1px solid rgba(151, 151, 151, 0.496575);
        border-right: 1px solid rgba(151, 151, 151, 0.496575);
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
    }

    &__select__deliver {
        background: none;
        border: none;
        outline: none;
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        border-left: 1px solid rgba(151, 151, 151, 0.496575);
        padding: 13px 14px 14px 24px;
    }

    &__btn {
        border: none;
        outline: none;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        padding: 14.5px 18px;
        margin-left: 14px;
        border-radius: 22px;
        background: #19D476;
        color: #FFFFFF;
        cursor: pointer;
    }
}

.opacity {
    opacity: 0.33;
}