.slider {
    background: rgba(182, 202, 251, 0.159986);
    box-shadow: inset 0px 21px 13px rgba(182, 202, 251, 0.225402);
    padding: 45px 0;

    &__info {
        div {
            display: flex;
            align-items: center;
            gap: 55px;
        }
    }
}

.swiper {
    padding: 26px 0;
    margin-bottom: 35px;

    &__info {
        border-right: 1px solid rgba(151, 151, 151, 0.356686);
        border-left: 1px solid rgba(151, 151, 151, 0.356686);
    }

    &__heading {
        font-size: 20px;
        line-height: 25px;
        color: #414D6A;
        display: flex;
        align-items: center;
        gap: 7px;
        margin-bottom: 25px;
    }

    &__card {

        &__title {
            font-weight: 700;
            font-size: 16px;
            line-height: 24px;
            color: #4E72CA;
        }

        &__text {
            font-weight: 700;
            font-size: 16px;
            line-height: 24px;
            color: #AAAAB6;
            display: flex;
            align-items: center;
            gap: 7px;
        }
    }
}