.add {

    &__header {

        &__heading {
            font-size: 24px;
            line-height: 30px;
            text-align: center;
            color: #000000;
            margin-bottom: 10px;
        }

        &__text {
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            text-align: center;
            color: #000000;
            margin-bottom: 37px;
        }

        &__cards {
            display: flex;
            align-items: center;
            justify-content: space-between;
            position: relative;
        }
    }

    &__menu {
        display: flex;
        align-items: center;
        background: #FFFFFF;
        border: 1px solid rgba(151, 151, 151, 0.185773);
        box-shadow: 3px 4px 14px rgba(0, 0, 0, 0.046139);
        padding: 10px 0 13px 24px;
        margin-top: 21px;
        position: relative;

        &::before {
            content: '';
            display: block;
            width: 24px;
            height: 24px;
            border-left: 1px solid rgba(151, 151, 151, 0.185773);
            border-bottom: 1px solid rgba(151, 151, 151, 0.185773);
            transform: rotate(135deg);
            position: absolute;
            bottom: 82%;
            left: 155px;
        }

        &__text {
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            color: #000000;
            margin-right: 20px;

            &--active {
                color: #2EB769;
            }
        }

        &__link {
            background: #FFFFFF;
            border: 1px solid #41D481;
            border-radius: 100px 5px 5px 100px;
            padding: 8px 19px 8px 0;
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            color: #000000;

            &__circle {
                border-radius: 50%;
                padding: 9px 8px 8px 9px;
                margin-right: 12px;
                background: #41D481;
            }
        }
    }

    &__info {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__form {
        margin-top: 58px;

        &__price {
            display: flex;
            align-items: flex-end;
            margin-bottom: 48px;

            &__info {
                display: flex;
                flex-direction: column;

                &__label {
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 17px;
                    color: #000;
                    margin-bottom: 13px;
                }

                &__input {
                    border: none;
                    outline: none;
                    border-bottom: 1px solid #979797;
                    padding-bottom: 7px;
                    margin-right: 43px;
                    font-weight: 500;
                    font-size: 19px;
                    line-height: 23px;
                    color: black;
                }
            }

            &__select {
                border: none;
                outline: none;
                border-bottom: 1px solid #979797;
                padding-bottom: 6px;
                font-style: normal;
                font-weight: 500;
                font-size: 17px;
                line-height: 21px;
                color: #000;
                margin-right: 26px;
            }

            &__wrapper {
                display: flex;
                align-items: center;

                &__checkbox {
                    width: 24px;
                    height: 24px;
                    margin-right: 11px;
                }

                &__label {
                    font-weight: 500;
                    font-size: 17px;
                    line-height: 21px;
                    color: #000;
                }
            }
        }

        &__numRooms {
            display: flex;
            align-items: flex-end;
            margin-bottom: 48px;

            &__info {
                display: flex;
                flex-direction: column;

                &__label {
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 17px;
                    color: #000;
                    margin-bottom: 13px;
                }

                &__input {
                    border: none;
                    outline: none;
                    border-bottom: 1px solid #979797;
                    padding-bottom: 7px;
                    margin-right: 43px;
                    font-weight: 500;
                    font-size: 19px;
                    line-height: 23px;
                    color: black;
                }
            }
        }

        &__heading {
            font-weight: 400;
            font-size: 14px;
            line-height: 17px;
            color: #000;
            margin-bottom: 13px;
        }

        &__location {
            margin-bottom: 48px;

            &__wrapper {
                display: flex;

                &__info {
                    display: flex;
                    align-items: center;
                    width: 33%;
                    margin-bottom: 33px;

                    &__checkbox {
                        width: 24px;
                        height: 24px;
                        margin-right: 11px;
                    }

                    &__label {
                        font-weight: 500;
                        font-size: 17px;
                        line-height: 21px;
                        color: #000;
                    }
                }

            }
        }

        &__feature {
            display: flex;
            align-items: flex-end;
            margin-bottom: 48px;

            &__info {
                display: flex;
                flex-direction: column;
                margin-bottom: 31px;

                &__label {
                    font-weight: 500;
                    font-size: 17px;
                    line-height: 21px;
                    color: #000;
                    margin-bottom: 13px;
                }

                &__input {
                    border: none;
                    outline: none;
                    border-bottom: 1px solid #979797;
                    padding-bottom: 7px;
                    margin-right: 43px;
                    font-weight: 500;
                    font-size: 19px;
                    line-height: 23px;
                    color: black;
                }
            }
        }

        &__state {
            margin-bottom: 48px;

            &__wrapper {
                display: flex;

                &__info {
                    display: flex;
                    align-items: center;
                    width: 33%;
                    margin-bottom: 33px;

                    &__checkbox {
                        width: 24px;
                        height: 24px;
                        margin-right: 11px;
                    }

                    &__label {
                        font-weight: 500;
                        font-size: 17px;
                        line-height: 21px;
                        color: #000;
                    }
                }

            }
        }

        &__type {
            display: flex;
            margin-bottom: 48px;

            &__info {
                display: flex;
                flex-direction: column;

                &:first-child &__select {
                    padding-right: 137px;
                }

                &__label {
                    font-weight: 500;
                    font-size: 17px;
                    line-height: 21px;
                    color: #000;
                    margin-bottom: 13px;
                }

                &__select {
                    border: none;
                    outline: none;
                    border-bottom: 1px solid #979797;
                    padding-bottom: 6px;
                    font-style: normal;
                    font-weight: 500;
                    font-size: 17px;
                    line-height: 21px;
                    color: #000;
                    margin-right: 26px;
                }
            }
        }

        &__heating {
            margin-bottom: 48px;

            &__wrapper {
                display: flex;

                &__info {
                    display: flex;
                    align-items: center;
                    width: 33%;
                    margin-bottom: 33px;

                    &__checkbox {
                        width: 24px;
                        height: 24px;
                        margin-right: 11px;
                    }

                    &__label {
                        font-weight: 500;
                        font-size: 17px;
                        line-height: 21px;
                        color: #000;
                    }
                }

            }
        }

        &__text {
            padding-bottom: 133px;
            font-weight: 500;
            font-size: 19px;
            line-height: 23px;
        }

        &__storage {
            padding-top: 10px;
            border-top: 1px solid #979797;

            &__heading {
                margin-bottom: 14px;
                font-weight: 500;
                font-size: 19px;
                line-height: 23px;
            }

            &__pictures {

                &__info {
                    display: flex;
                    gap: 37px;

                    &__wrapper {
                        margin-bottom: 23px;
                        
                        &__file {
                            display: none;
                        }

                        &__label {
                            background-color: rgba(180, 226, 225, .350232);
                            border: 1px solid #50e3c2;
                            box-sizing: border-box;
                            border-radius: 9px;
                            width: 107px;
                            height: 107px;
                            position: relative;
                            display: block;

                            &::before {
                                content: "";
                                position: absolute;
                                top: 0;
                                bottom: 0;
                                right: 0;
                                left: 0;
                                margin: auto;
                                width: 42px;
                                height: 36px;
                                background-repeat: no-repeat;
                                background-image: url('../../assets/image/camera.svg');
                            }
                        }
                    }
                }
            }
        }

        &__choose {

            &__heading {
                margin-bottom: 15px;
                font-weight: 500;
                font-size: 24px;
                line-height: 29px;
                color: #000;
            }

            &__info {
                display: flex;
                margin-bottom: 20px;

                &__wrapper {
                    margin-right: 30px;
                    display: flex;
                    flex-direction: column;

                    &__label {
                        font-weight: 500;
                        font-size: 17px;
                        line-height: 21px;
                        color: #000;
                        margin-bottom: 13px;
                    }

                    &__select {
                        border: none;
                        outline: none;
                        border-bottom: 1px solid #979797;
                        padding-bottom: 6px;
                        font-style: normal;
                        font-weight: 500;
                        font-size: 17px;
                        line-height: 21px;
                        color: #000;
                        margin-right: 26px;
                        padding-right: 136px;
                    }
                }
            }

            &__iframe {
                margin-bottom: 22px;
            }

            &__login {

                &__info {
                    max-width: 309px;
                    margin-bottom: 31px;

                    &__label {
                        font-weight: 500;
                        font-size: 17px;
                        line-height: 21px;
                        color: #000;
                    }

                    &__input {
                        border: none;
                        outline: none;
                        border-bottom: 1px solid #979797;
                        padding-bottom: 7px;
                        font-weight: 500;
                        font-size: 19px;
                        line-height: 23px;
                        color: black;
                        margin-top: 13px;
                    }
                }

                &__wrapper {
                    display: flex;
                    align-items: center;

                    &__btn {
                        background-color: #19d476;
                        border-radius: 13px;
                        padding: 21px 46px;
                        margin-right: 67px;
                        font-style: normal;
                        font-weight: 500;
                        font-size: 19px;
                        line-height: 23px;
                        color: #000;
                        border: none;
                        outline: none;
                        cursor: pointer;
                    }

                    &__link {
                        font-style: normal;
                        font-weight: 500;
                        font-size: 19px;
                        line-height: 23px;
                        text-decoration-line: underline;
                        color: #4e72ca;
                    }
                }
            }
        }
    }
}