.header {
    box-shadow: 12px 4px 24px rgba(0, 0, 0, 0.0647645);
    margin-bottom: 40px;

    &__container {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__logo__wrapper {
        display: flex;
        align-items: center;

        &__text {
            width: 152px;
            margin-left: 14px;
            font-weight: 700;
            font-size: 15px;
            line-height: 17px;
        }
    }

    &__navbar {

        &__list {
            width: 425px;
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        &__item {

            &__link {
                text-decoration: none;
                font-weight: 500;
                font-size: 15px;
                line-height: 18px;
                color: #000000;
            }
        }
    }

    &__language__wrapper {
        display: flex;
        align-items: center;
        gap: 15px;

        .--active {
            position: relative;

            &::before {
                content: '';
                position: absolute;
                left: calc(50% - 16px);
                top: calc(50%);
                display: block;
                width: 35px;
                height: 3px;
                transform: rotate(-45deg);
                background-color: rgba(246, 218, 194, 0.831324);
                z-index: -999;
            }
        }

        &__rus__btn {
            padding: 0;
            outline: none;
            border: none;
            background: none;
            cursor: pointer;
        }

        &__uz__btn {
            padding: 0;
            outline: none;
            border: none;
            background: none;
            cursor: pointer;
        }
    }

    &__login__wrapper {
        display: flex;
        align-items: center;
        gap: 18px;
        color: #4E72CA;

        &__link {
            text-decoration: none;
            font-weight: 700;
            font-size: 17px;
            line-height: 21px;
            color: #4E72CA;
        }

        &__reg {
            text-decoration: none;
            font-weight: 700;
            font-size: 17px;
            line-height: 21px;
            padding-top: 3px;
            color: #4E72CA;

            &::after {
                content: '';
                display: block;
                width: 100px;
                height: 3px;
                opacity: 0.66;
                background-color: #4E72CA;
            }
        }
    }

    &__profile {
        display: flex;
        align-items: center;
        gap: 13px;
        position: relative;

        &:hover &__btn {
            display: inline-block;
        }

        &__wrapper {
            width: 41px;
            height: 41px;
            border: 1px solid #979797;
            border-radius: 20.5px;
        }

        &__heading {
            font-weight: 700;
            color: #000000;
        }

        &__img {
            width: 41px;
            height: 41px;
            border-radius: 20.5px; 
        }

        &__btn {
            display: none;
            background: dimgray;
            color: white;
            border: none;
            border-radius: 15px;
            box-shadow: 0 0 2px 2px rgb(255, 0, 0);
            padding: 5px 10px;
            position: absolute;
            top: 40px;
            left: 70px;
        }
    }
}