.card {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__img {
        width: 75px;
        height: 75px;
        border-radius: 20px;
        object-fit: contain;
        display: flex;
        align-items: center;
        justify-content: center;
        vertical-align: middle;
        position: relative;
    }

    &__text {
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        color: #4A4A4A;
    }
}

.opacity {
    opacity: 0.33;
}

.nike {
    &::before {
        content: '';
        display: block;
        width: 39px;
        height: 39px;
        background-image: url('../../assets/image/nike.svg');
        background-repeat: no-repeat;
        background-size: contain;
        position: absolute;
        left: 49px;
        bottom: 41px;
    }
}

