.product {

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        margin-top: 30px;
        margin-bottom: 31px;

        &::before {
            content: '';
            display: block;
            width: 34px;
            height: 34px;
            margin-right: 10px;
            background-image: url(../../assets/image/arrowCross.svg);
            background-repeat: no-repeat;
            background-size: contain;
            position: absolute;
            top: calc(50% - 25%);
            right: 100%;
        }

        &::after {
            content: '';
            display: block;
            width: 34px;
            height: 34px;
            margin-left: 10px;
            background-image: url(../../assets/image/arrowCross.svg);
            background-repeat: no-repeat;
            background-size: contain;
            position: absolute;
            top: calc(50% - 25%);
            transform: rotate(180deg);
            left: 100%;
        }
    }

    &__cards {

        &__wrapper {

            &__link {
                text-decoration: none;
                font-weight: 700;
                font-size: 24px;
                line-height: 30px;
                color: #9B9B9B;
                margin-bottom: 23px;
                padding-bottom: 8.5px;

                &:not(:last-child) {
                    margin-right: 77px;
                }
            }

            &__line {
                display: block;
                border-bottom: 2px solid #D5D5D5;
                padding-top: 8px;
            }
        }

        &__list {
            display: flex;
            flex-wrap: wrap;
        }
    }

    &__wrapper {

        &__link {
            text-decoration: none;
            font-weight: 700;
            font-size: 24px;
            line-height: 30px;
            color: #9B9B9B;
            margin-bottom: 23px;
            padding-bottom: 9px;

            &:not(:last-child) {
                margin-right: 77px;
            }
        }

        &__line {
            display: block;
            border-bottom: 2px solid #D5D5D5;
            padding-top: 8px;
        }
    }
}

.active {
    font-weight: 900;
    border-bottom: 2px solid #4E72CA;
    color: #4E72CA;
}