.cardRender {

    &__list {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-top: 23px;
        border-bottom: 2px solid #D5D5D5;
    }

    &__text {
        margin-top: 18px;
        font-size: 15px;
        line-height: 18px;
        color: #4E72CA;
        display: flex;
        align-items: center;
        justify-content: center;

        &__arrow {
            transform: rotate(90deg);
            margin-left: 9px;
        }
    }
}