.login {
    display: flex;
    // align-items: center;
    justify-content: space-between;

    &__left {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 85px;

        &__heading {
            font-size: 24px;
            line-height: 30px;
            margin-bottom: 22px;
            text-transform: uppercase;
            color: #000000;
        }

        &__text {
            text-align: center;
            width: 334px;
            font-weight: 500;
            font-size: 17px;
            line-height: 23px;
            margin-bottom: 11px;
            color: #304FFF;
        }

        &__form {

            &__label {
                font-weight: 500;
                font-size: 17px;
                line-height: 21px;
                color: #787993;

            }

            &__input {
                width: 330px;
                display: block;
                padding: 20px 24px;
                margin-bottom: 23px;
                margin-top: 7px;
            }

            &__btn {
                border: none;
                width: 100%;
                font-weight: 500;
                font-size: 17px;
                line-height: 21px;
                border-radius: 5px;
                padding: 20px 0;
                margin-bottom: 49px;
                text-align: center;
                color: #FFFFFF;
                background: #41D481;
            }

            &__text {
                text-align: center;
                margin-bottom: 16px;
                font-weight: 500;
                font-size: 14px;
                line-height: 17px;
                color: #787993;
                position: relative;

                &::before {
                    content: '';
                    display: block;
                    width: 40%;
                    border: 1px solid #979797;
                    position: absolute;
                    top: 50%;
                    left: 0;
                }

                &::after {
                    content: '';
                    display: block;
                    width: 40%;
                    border: 1px solid #979797;
                    position: absolute;
                    top: 50%;
                    right: 0;
                }
            }

            &__link {
                font-weight: 700;
                font-size: 16px;
                line-height: 20px;
                color: #4E72CA;
                text-align: center;
            }
        }
    }

    &__right {
        width: 50%;
        height: 100%;
        position: relative;

        &__modal {
            background: #FFFFFF;
            padding: 78px 20px 40px 42px;
            border-radius: 220px 220px 220px 25px;
            position: absolute;
            top: 50%;
            left: 61px;

            &__heading {
                width: 213px;
                font-weight: 700;
                font-size: 19px;
                line-height: 21px;
                color: #787993;
                margin-top: 8px;
                margin-bottom: 19px;
            }

            &__btn {
                font-weight: 700;
                font-size: 16px;
                line-height: 20px;
                border: 2px solid #41D481;
                border-radius: 5px;
                color: #41D481;
                background-color: #fff;
                padding: 10px 18px;
            }
        }
    }
}