.profile {

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom: 2px solid rgba(151, 151, 151, 0.34);
        padding-bottom: 8px;

        &__heading {
            font-size: 17px;
            line-height: 21px;
            color: #414D6A;

        }

        &__info {
            display: flex;
            align-items: center;
            gap: 26px;

            &__text {
                font-weight: 500;
                font-size: 17px;
                line-height: 21px;
                color: #304FFF;

            }

            &__btn {
                font-weight: 500;
                font-size: 16px;
                line-height: 19px;
                color: #000000;
                background: #FFFFFF;
                border: 1px solid #41D481;
                border-radius: 100px 5px 5px 100px;
                padding-right: 22px;
                padding-left: 0;
                cursor: pointer;
                display: flex;
                align-items: center;

                &__circle {
                    background: #41D481;
                    text-align: center;
                    font-size: 28px;
                    line-height: 33px;
                    border-radius: 50%;
                    padding: 6px 13px 5px 15px;
                    margin-right: 12px;
                    color: #000;
                }
            }
        }
    }

    &__wrapper {
        padding-top: 16px;
        // border-bottom: 2px solid rgba(151, 151, 151, 0.600459);
        & &__line {
            align-items: flex-end;
            justify-content: center;
        }
        

        &__link {
            font-weight: 500;
            font-size: 17px;
            line-height: 21px;
            color: #414D6A;
            padding-bottom: 14px;
            margin-right: 100px;

            &:last-child {
                margin-right: 0;
            }
        }

        &__line {
            display: block;
            border-bottom: 2px solid rgba(151, 151, 151, 0.600459);
            padding-top: 14px;
        }
    }

    &__text {
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #000000;
        display: flex;
        align-items: center;
        margin-left: 22px;
        gap: 290px;
        padding-top: 15px;
        padding-bottom: 11px;

        &__left {
            display: flex;
            gap: 32px;
        }

        &__right {
            display: flex;
            gap: 155px;
        }
    }
}