.productsPage {

    &__top {
        background-color: #FFFFFF;
        border: 1px solid rgba(151, 151, 151, 0.496575);
        border-radius: 6px;
        padding: 16px 0 16px 17px;
        position: relative;
        margin-bottom: 12px;

        &::before {
            content: '';
            width: 24px;
            height: 24px;
            position: absolute;
            top: 15px;
            display: inline-block;
            background-image: url(./../../assets/image/lupa__icon.svg);
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
        }

        &__text {
            font-weight: 500;
            line-height: 19px;
            color: #787993;
            padding-left: 40px;
        }

        &__link {
            font-weight: 700;
            line-height: 19px;
            color: #2979FF;
            text-decoration: underline;
        }
    }

    &__navigate {
        display: flex;
        align-items: center;
        gap: 14px;
        margin-bottom: 25px;

        &__link__1 {
            font-weight: 700;
            font-size: 13px;
            line-height: 16px;
            background: #E5E3FF;
            border-radius: 3px;
            color: #704FFF;
            padding: 2px 10px;
        }

        &__link__2 {
            font-weight: 700;
            font-size: 13px;
            line-height: 16px;
            background: #E4F6FF;
            border-radius: 3px;
            color: #57B4E2;
            padding: 2px 12px;
        }

        &__link__3 {
            font-weight: 700;
            font-size: 13px;
            line-height: 16px;
            background: #D2FFDB;
            border-radius: 3px;
            color: #47B95E;
            padding: 2px 12px;
        }

        &__link__4 {
            font-weight: 700;
            font-size: 13px;
            line-height: 16px;
            background: #F6DAC2;
            border-radius: 3px;
            color: #874C1A;
            padding: 2px 13px;
        }

        &__link__5 {
            width: 103px;
            font-weight: 700;
            font-size: 13px;
            line-height: 16px;
            background: #D8D8D8;
            border-radius: 3px;
            color: #787993;
            padding: 2px 0 3px 13px;
            display: flex;
            align-items: center;
            gap: 14px;

            &::after {
                content: '';
                display: block;
                width: 7px;
                height: 10px;
                background-image: url('../../assets/image/pin.svg');
                background-repeat: no-repeat;
                background-size: contain;
            }
        }

        &__link__6 {
            width: 87px;
            font-weight: 700;
            font-size: 13px;
            line-height: 16px;
            background: #D8D8D8;
            border-radius: 3px;
            color: #787993;
            padding: 2px 0 3px 13px;
            display: flex;
            align-items: center;
            gap: 8px;

            &::after {
                content: '';
                display: block;
                width: 7px;
                height: 10px;
                background-image: url('../../assets/image/pin.svg');
                background-repeat: no-repeat;
                background-size: contain;
            }
        }

        &__link__7 {
            width: 114px;
            font-weight: 700;
            font-size: 13px;
            line-height: 16px;
            background: #D8D8D8;
            border-radius: 3px;
            color: #787993;
            padding: 2px 0 3px 13px;
            display: flex;
            align-items: center;
            gap: 8px;

            &::after {
                content: '';
                display: block;
                width: 7px;
                height: 10px;
                background-image: url('../../assets/image/pin.svg');
                background-repeat: no-repeat;
                background-size: contain;
            }
        }
    }

    &__rubric {
        display: flex;
        align-items: center;
        gap: 54px;
        margin-bottom: 23px;

        &__text {
            font-size: 16px;
            line-height: 19px;
            color: #787993;
        }

        &__info {
            display: flex;
            align-items: center;
            gap: 93px;
            background: #FFFFFF;
            border: 1px solid rgba(151, 151, 151, 0.496575);
            border-radius: 27px;
            padding: 7px 23px 6px 8px;

            &__wrapper {
                display: flex;
                align-items: center;
                gap: 7px;

                &__btn {
                    font-weight: 500;
                    font-size: 15px;
                    line-height: 18px;
                    color: #2979FF;
                    background: #EBF5FF;
                    border: 1px solid #B3D9FF;
                    border-radius: 20.5px;
                    cursor: pointer;
                    padding-left: 20px;
                    padding-right: 0;
                    display: flex;
                    align-items: center;
                    gap: 6px;

                    &::after {
                        content: '';
                        display: block;
                        width: 41px;
                        height: 41px;
                        background-image: url('../../assets/image/cross.svg');
                        background-repeat: no-repeat;
                        background-size: 50%;
                        background-position: center;
                        border: 1px solid #B3D9FF;
                        border-radius: 20.5px;
                    }
                }

                &__arrow {
                    width: 8px;
                    height: 17px;
                    transform: rotate(90deg);
                }
            }
        }
    }

    &__choose {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 15px;

        &__text {
            font-size: 16px;
            line-height: 19px;
            color: #787993;
        }

        &__range {
            width: 923px;
            height: 5px;
        }
    }

    &__menu {
        display: flex;
        align-items: center;
        justify-content: space-between;

        &__info {
            display: flex;
            align-items: center;
            gap: 31px;
            margin-bottom: 29px;

            &__select1 {
                border: none;
                outline: none;
                font-weight: 700;
                font-size: 14px;
                line-height: 17px;
                color: #414D6A;
                padding-right: 42px;
                padding-left: 19px;
                padding-top: 15px;
                padding-bottom: 14px;
            }

            &__select2 {
                border: none;
                outline: none;
                font-weight: 700;
                font-size: 14px;
                line-height: 17px;
                color: #414D6A;
                padding-right: 14px;
                padding-left: 19px;
                padding-top: 15px;
                padding-bottom: 14px;
            }

            &__select3 {
                border: none;
                outline: none;
                font-weight: 700;
                font-size: 14px;
                line-height: 17px;
                color: #414D6A;
                padding-right: 63px;
                padding-left: 19px;
                padding-top: 15px;
                padding-bottom: 14px;
            }
        }

        &__btn {
            border: none;
            font-weight: 700;
            font-size: 15px;
            line-height: 19px;
            color: #FFFFFF;
            background: #304FFF;
            box-shadow: 3px 6px 14px #9CC1FF;
            border-radius: 22px;
            padding: 14px 12px;
        }
    }

    &__line {
        border-bottom: 2px solid rgba(151, 151, 151, 0.393739);
        display: block;
        margin-bottom: 29px;
    }

    &__heading {
        font-size: 20px;
        line-height: 25px;
        color: #41D481;  
        margin-bottom: 18px;      
    }
}