.infoProduct {

  &__top {
    margin-bottom: 14px;

    &__text {
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      color: #787993;
      display: flex;
      align-items: center;
      gap: 12px;
      margin-top: 24px;
      margin-bottom: 20px;
    }

    &__heading {
      font-weight: 500;
      font-size: 34px;
      line-height: 41px;
      color: #414D6A;
    }
  }

  &__info {
    display: flex;
    justify-content: space-between;
    margin-bottom: 98px;

    &__right {
      width: 559px;

      &__text {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #414D6A;
        margin-bottom: 17px;
      }

      &__heading {
        font-weight: 500;
        font-size: 40px;
        line-height: 49px;
        color: #444444;
        margin-bottom: 8px;
      }

      &__link {
        display: block;
        font-weight: 700;
        font-size: 14px;
        line-height: 16px;
        color: #4E72CA;
        text-decoration-line: underline;
        margin-bottom: 14px;
      }

      &__btn {
        border: none;
        background: #65E99F;
        border-radius: 18.5px;
        padding: 9px 0 8px 23px;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
        display: block;
        margin-bottom: 7px;
        cursor: pointer;

        &__num {
          background: #41D481;
          border-radius: 18.5px;
          padding: 10px 26px 10px 19px;
          font-weight: 400;
          font-size: 15px;
          line-height: 18px;
          margin-left: 12px;
        }
      }

      &__text__wrapper {
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        display: flex;
        gap: 7px;
        margin-bottom: 12px;

        &__heading {
          font-weight: 700;
          font-size: 14px;
          line-height: 17px;
          text-decoration-line: underline;
          color: #2AAE63;
        }
      }

      &__btn__box {
        display: flex;
        align-items: center;
        gap: 11px;
        margin-bottom: 25px;

        &__1 {
          border: none;
          font-size: 14px;
          line-height: 16px;
          color: #FFFFFF;
          background: #304FFF;
          border-radius: 23.5px;
          padding: 15px 28px;
          cursor: pointer;
        }

        &__2 {
          border: none;
          font-size: 14px;
          line-height: 16px;
          color: #FFFFFF;
          background: #4E72CA;
          border-radius: 23.5px;
          padding: 15px 32px;
          cursor: pointer;
        }
      }

      &__table__1 {
        border-bottom: 2px solid rgba(151, 151, 151, 0.250226);
        display: flex;

        &__text__1 {
          span {
            display: block;
            margin-top: 5px;
          }

          font-size: 14px;
          line-height: 16px;
          color: #444444;
          padding: 13px 32px 22px 17px;
          border-left: 2px solid rgba(151, 151, 151, 0.250226);
          border-right: 1px solid rgba(151, 151, 151, 0.250226);
        }

        &__text__2 {
          span {
            display: block;
            margin-top: 5px;
          }

          font-size: 14px;
          line-height: 16px;
          color: #444444;
          padding: 13px 63px 22px 22px;
          border-left: 1px solid rgba(151, 151, 151, 0.250226);
          border-right: 1px solid rgba(151, 151, 151, 0.250226);
        }

        &__text__3 {
