          color: #444444;
          padding: 13px 89px 22px 22px;
          border-left: 1px solid rgba(151, 151, 151, 0.250226);
          border-right: 1px solid rgba(151, 151, 151, 0.250226);
        }

        &__text__3 {
          span {
