          color: #444444;
          padding: 14px 81px 48px 22px;
          border-left: 1px solid rgba(151, 151, 151, 0.250226);
          border-right: 1px solid rgba(151, 151, 151, 0.250226);
        }

        &__text__3 {
          span {
            display: block;
            margin-top: 5px;
          }

          font-size: 14px;
          line-height: 16px;
          color: #444444;
          padding: 14px 48px 48px 22px;
          border-left: 1px solid rgba(151, 151, 151, 0.250226);
        }
      }

      &__bottom {
        margin-bottom: 40px;

        &__text {
          font-size: 14px;
          line-height: 16px;
          color: #444444;
        }

        &__link {
          font-weight: 500;
          font-size: 18px;
          line-height: 22px;
          text-decoration-line: underline;
          color: #4E72CA;
        }

        &__description {
          margin-bottom: 50px;
          font-weight: 500;
          font-size: 17px;
          line-height: 25px;
          color: #414D6A;
        }

        &__words {
          margin-bottom: 19px;
          font-weight: 500;
          font-size: 17px;
          line-height: 25px;
          color: #414D6A;
        }

        &__num {
          span {
            color: #D0021B;
          }

          font-weight: 500;
          font-size: 14px;
          line-height: 17px;
          display: flex;
          align-items: center;
          gap: 200px;
        }
      }
    }
  }

  &__wrapper {

    &__link {
      text-decoration: none;
      font-weight: 700;
      font-size: 24px;
      line-height: 30px;
      color: #9B9B9B;
      margin-bottom: 23px;
      padding-bottom: 10px;

      &:not(:last-child) {
        margin-right: 77px;
      }
    }

    &__line {
      display: block;
      border-bottom: 2px solid #D5D5D5;
      padding-top: 8px;
    }
  }
}