.profilPost {
    background: #FFFFFF;
    margin-bottom: 5px;
    &:last-child {
        margin-bottom: 0;
    }

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom: 1px solid rgba(151, 151, 151, 0.246886);
        padding: 17px;

        &__wrapper {
            display: flex;
            align-items: center;

            &__heading {
                font-weight: 500;
                font-size: 17px;
                line-height: 21px;
                margin-left: 16px;
                color: #414D6A;
            }
        }

        &__price__text {
            font-weight: 500;
            font-size: 15px;
            line-height: 18px;
            color: #414D6A;

            &__num {
                display: block;
            }
        }

        &__data {
            font-weight: 500;
            font-size: 14px;
            line-height: 17px;
            color: #414D6A;

            &__text {
                display: block;
            }
        }

        &__right__wrapper {
            display: flex;
            align-items: center;
            gap: 22px;

            &__btn {
                background: #FFFFFF;
                border: 1px solid #41D481;
                border-radius: 100px 5px 5px 100px;
                font-weight: 500;
                font-size: 16px;
                line-height: 19px;
                padding: 7px 20px 9px 47px;
                color: #000000;
                position: relative;

                &::before {
                    content: '';
                    display: block;
                    width: 34px;
                    height: 34px;
                    background-image: url(../../assets/image/arr.svg);
                    position: absolute;
                    top: 1px;
                    left: -1px;
                }

                &__primary {
                    background: #FFFFFF;
                    border: 1px solid #304FFF;
                    border-radius: 100px 5px 5px 100px;
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 19px;
                    padding: 7px 20px 9px 47px;
                    color: #000000;
                    position: relative;

                    &::before {
                        content: '';
                        display: block;
                        width: 34px;
                        height: 34px;
                        background-image: url(../../assets/image/arr2.svg);
                        position: absolute;
                        top: 1px;
                        left: -1px;
                    }
                }
            }
        }
    }

    &__bottom__text {
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #414D6A;  
        display: flex;
        align-items: center;
        gap: 26px;    
        padding: 8px 0 8px 23px;      

        &--active {
            color: #304FFF;
        }
    }
}