.productCard {
    width: 265px;
    margin-bottom: 35px;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: space-between;

    &__img {
        width: 265px;
        height: 190px;
        vertical-align: middle;
        object-fit: contain;
        margin-bottom: 13px;
    }

    &__title {
        display: block;
        font-size: 15px;
        line-height: 18px;
        color: #000000;
        margin-bottom: 10px;
        padding: 0 3px 0 5px;
    }

    &__time {
        display: block;
        font-size: 12px;
        line-height: 14px;
        color: #4A4A4A;  
        margin-bottom: 4px;      
        padding-left: 5px;
    }

    &__price {
        display: block;
        font-weight: 700;
        font-size: 17px;
        line-height: 21px;
        color: #000000;    
        padding-left: 5px;   
    }
}